<template>
    <section class="container">
        <div
            ref="slider"
            class="slider">
            <div class="slider-group" ref="sliderGroup">
                <slot></slot>
            </div>
        </div>
        <div class="dots" ref="dots">
            <span :class="{active: currentPageIndex === index }" class="dot" v-for="(item, index) in dots"></span>
        </div>
    </section>
</template>

<script>
    import BScroll from 'better-scroll'
    import {addClass} from '../methods/dom'

    export default {
        name: "PublicSlider",
        props: {
            loop: {
                type: Boolean,
                default: true
            },
            autoplay: {
                type: Boolean,
                default: true
            },
            interval: {
                type: Number,
                default: 4000
            },
        },
        data() {
            return {
                dots: [],
                currentPageIndex: 0
            }
        },
        // mountedHTMLbetter-scroll
        mounted() {
            // DOM20ms,14ms
            setTimeout(()=>{
                this._setSliderWidth() // dots_initSlider
                this._initDots()
                this._initSlider()

                if (this.autoplay) {
                    this._play()
                }
            },20)

            window.addEventListener('resize',()=>{
                if (!this.slider){ // better-scroll
                    return
                }
                this._setSliderWidth(true) // isResizetrue

                this.slider.refresh() //  better-scroll DOM 
            })
        },
        destroyed() {
            clearTimeout(this.timer) // 
        },
        methods:{
            _setSliderWidth(isResize){
                this.children = this.$refs.sliderGroup.children // sliderGroup
                let width = 0

                // 
                let sliderWidth = this.$refs.slider.clientWidth

                for(let i = 0; i<this.children.length;i++){
                    let child = this.children[i]
                    addClass(child,'slider-item') // slider -itemclassName
                    // 
                    child.style.width = sliderWidth + 'px'
                    // 
                    width += sliderWidth
                }
                if (this.loop&!isResize){
                    width += 2*sliderWidth // 2
                }
                // 
                this.$refs.sliderGroup.style.width = width + 'px'
            },
            _initDots(){
                this.dots = new Array(this.children.length) // 
                if (this.dots<2) {
                    this.$refs.dots.style.display = "none"
                }
            },
            _initSlider(){
                // better-scroll https://ustbhuangyi.github.io/better-scroll/doc/zh-hans/options.html
                this.slider = new BScroll(this.$refs.slider,{
                    scrollX:true,
                    scrollY:true,
                    momentum: false, // 
                    snap:{
                        loop:this.loop,
                        threshold: 0.3, // (30%)
                        speed: 400 // 
                    }
                })

                this.slider.on('scrollEnd',()=>{
                    let pageIndex = this.slider.getCurrentPage().pageX // better-scrollindex
                    this.currentPageIndex = pageIndex
                    if (this.autoplay) {
                        clearTimeout(this.timer) // 
                        this._play()
                    }
                })
            },
            _play(){
                this.timer = setTimeout(()=>{
                    this.slider.next(400) // _initSlildersnap.speed
                },this.interval)
            },
            refresh(){
                this.slider && this.slider.refresh()
            }
        }
    };
</script>

<style lang="scss" scoped>
    .container{
        position: relative;
    }
    .slider{
        min-height: 1px;
        overflow: hidden;
    }
    .slider-group{
        position: relative;
        overflow: hidden;
        white-space: nowrap;
    }
    .slider-item{
        float: left;
        box-sizing: border-box;
        overflow: hidden;
        text-align: center;

        // slotaimg
        a{
            display: block;
            width: 100%;
            overflow: hidden;
        }

        img{
            display: block;
            width: 100%;
        }
    }

    .dots{
        position: absolute;
        right: 0;
        left: 0;
        bottom: 12px;
        text-align: center;
        font-size: 0;

        .dot{
            display: inline-block;
            margin: 0 4px;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: rgba(255, 255, 255, 0.5);

            &.active{
                width: 20px;
                border-radius: 5px;
                background-color: rgba(255, 255, 255, 0.8);
            }
        }
    }
</style>





